/* Hero Section Enhancements - 2025 Update (refined) */

:root {
  --hero-gradient-start: #4f46e5; /* Indigo 600 */
  --hero-gradient-end: #06b6d4;   /* Cyan 500 */
  --hero-accent: #f43f5e;         /* Rose 500 */
  --hero-text-gradient: linear-gradient(135deg, var(--hero-gradient-start), var(--hero-gradient-end));
  --hero-shadow-color: rgba(79, 70, 229, 0.25);
  --glass-bg: rgba(255, 255, 255, 0.7);
  --glass-border: rgba(255, 255, 255, 0.5);
  --glass-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.07);
  --particle-color: rgba(79, 70, 229, 0.15);
  --hero-bg-mesh:
      radial-gradient(at 0% 0%, hsla(253,16%,96%,1) 0, transparent 50%),
      radial-gradient(at 50% 0%, hsla(225,39%,97%,1) 0, transparent 50%),
      radial-gradient(at 100% 0%, hsla(339,49%,97%,1) 0, transparent 50%);

  /* Layout & motion tuning */
  --hero-max-width: 1280px;
  --hero-content-max: 56ch;
  --hero-container-pad: clamp(1rem, 3vw, 2.5rem);
  --hero-radius: 24px;
  --hero-blur: 16px;
  --hero-ease: cubic-bezier(0.16, 1, 0.3, 1);
  --hero-float: 14px;
  --hero-shadow-strong: 0 24px 60px rgba(31, 38, 135, 0.16);

  /* Premium depth layers */
  --hero-aurora-opacity: 0.55;
  --hero-grid-opacity: 0.25;
  --hero-ring-opacity: 0.7;
  --hero-glow-1: rgba(79, 70, 229, 0.24);
  --hero-glow-2: rgba(6, 182, 212, 0.18);
  --hero-glow-3: rgba(244, 63, 94, 0.12);
}

@media (prefers-color-scheme: dark) {
  :root {
    --glass-bg: rgba(15, 23, 42, 0.4);
    --glass-border: rgba(255, 255, 255, 0.08);
    --glass-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.4);
    --particle-color: rgba(255, 255, 255, 0.1);
    --hero-bg-mesh:
        radial-gradient(at 0% 0%, hsla(253,16%,10%,1) 0, transparent 50%),
        radial-gradient(at 50% 0%, hsla(225,39%,12%,1) 0, transparent 50%),
        radial-gradient(at 100% 0%, hsla(339,49%,12%,1) 0, transparent 50%);
  }
}

/* Accessibility & robustness */
:where(.hero-btn, .social-links-hero a, .scroll-down-indicator):focus-visible {
  outline: 3px solid var(--hero-accent);
  outline-offset: 3px;
}

/* Improve anchor tap targets on touch devices */
@media (pointer: coarse) {
  .hero-btn,
  .social-links-hero a {
    min-height: 44px;
  }
}

/* Fallback when backdrop-filter isn't supported */
@supports not ((backdrop-filter: blur(1px)) or (-webkit-backdrop-filter: blur(1px))) {
  .hero-content-wrapper,
  .hero-btn.secondary,
  .social-links-hero a,
  .status-badge {
    background: rgba(255, 255, 255, 0.92);
  }

  @media (prefers-color-scheme: dark) {
    .hero-content-wrapper,
    .hero-btn.secondary,
    .social-links-hero a,
    .status-badge {
      background: rgba(15, 23, 42, 0.85);
    }
  }
}

/* Respect user preference to reduce motion (complete) */
@media (prefers-reduced-motion: reduce) {
  .hero,
  .hero::before,
  .hero::after,
  .hero .profile-image,
  .hero-image-wrapper,
  .hero-image-wrapper::before,
  .hero-image-wrapper::after,
  .rtl-text,
  .hero .tagline .cursor,
  .scroll-down-indicator .wheel,
  .scroll-down-indicator .arrow-scroll span,
  .status-dot {
    animation: none !important;
    transition: none !important;
    transform: none !important;
  }

  /* Avoid motion from background-position animation and heavy effects */
  .hero {
    background-position: 50% 50% !important;
  }
}

/* =========================================================
   HERO BASE
   ========================================================= */

.hero {
  position: relative;
  padding: 9rem 1rem 6rem;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  min-height: 100vh;
  min-height: 100svh;

  /* safer containment: don't restrict layout calculations for positioned children */
  contain: paint;

  background-color: var(--bg-color, #f8fafc);
  background-image:
    radial-gradient(at 40% 20%, var(--hero-glow-1) 0px, transparent 55%),
    radial-gradient(at 80% 0%, var(--hero-glow-2) 0px, transparent 55%),
    radial-gradient(at 0% 50%, var(--hero-glow-3) 0px, transparent 55%);
  background-size: 140% 140%;
  animation: hero-bg-pulse 28s ease-in-out infinite alternate;
}

/* Safe-area padding for notches */
@supports (padding: max(0px)) {
  .hero {
    padding-left: max(1rem, env(safe-area-inset-left));
    padding-right: max(1rem, env(safe-area-inset-right));
  }
}

.hero::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)' opacity='0.05'/%3E%3C/svg%3E");
  opacity: 0.4;
  pointer-events: none;
  z-index: 0;
}

/* Aurora layer */
.hero::after {
  content: "";
  position: absolute;
  inset: -20%;
  background:
    radial-gradient(closest-side at 20% 25%, rgba(79, 70, 229, 0.35), transparent 60%),
    radial-gradient(closest-side at 85% 20%, rgba(6, 182, 212, 0.28), transparent 55%),
    radial-gradient(closest-side at 65% 85%, rgba(244, 63, 94, 0.18), transparent 60%);
  filter: blur(36px) saturate(115%);
  opacity: var(--hero-aurora-opacity);
  z-index: 0;
  pointer-events: none;
  transform: translate3d(0, 0, 0);
  animation: hero-aurora 18s var(--hero-ease) infinite alternate;
}

@keyframes hero-aurora {
  0% { transform: translate3d(-1.5%, -1%, 0) scale(1.02); }
  100% { transform: translate3d(1.5%, 1.25%, 0) scale(1.06); }
}

@keyframes hero-bg-pulse {
  0% { background-position: 0% 0%; }
  100% { background-position: 100% 100%; }
}

#hero-particles {
  position: absolute;
  inset: 0;
  z-index: 1;
  pointer-events: none;
}

.hero-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 4rem;
  align-items: center;
  max-width: var(--hero-max-width);
  margin: 0 auto;
  width: 100%;
  z-index: 2;
  padding: 0 var(--hero-container-pad);
  position: relative;
}

/* Subtle grid texture behind content */
.hero-grid::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image:
    linear-gradient(to right, rgba(99, 102, 241, 0.08) 1px, transparent 1px),
    linear-gradient(to bottom, rgba(6, 182, 212, 0.06) 1px, transparent 1px);
  background-size: 48px 48px;
  opacity: var(--hero-grid-opacity);
  mask-image: radial-gradient(circle at 50% 35%, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 62%);
  pointer-events: none;
  z-index: -1;
}

@media (min-width: 992px) {
  .hero-grid {
    grid-template-columns: 1.2fr 1fr;
    gap: 5rem;
  }
}

/* Mobile Optimizations */
@media (max-width: 991px) {
  .hero {
    padding: 5rem 0 3rem;
    min-height: auto;
  }

  .hero-grid {
    gap: 1.5rem;
    padding: 0;
    width: 100%;
  }

  .hero-image-wrapper {
    order: -1;
    margin-bottom: 0;
  }

  .hero .profile-image {
    width: 160px;
    height: 160px;
    box-shadow: 0 10px 25px var(--hero-shadow-color);
  }

  .hero-content-wrapper {
    margin: 0 1rem;
    width: auto;
    padding: 1.5rem 1rem;
    text-align: center;
    background: var(--glass-bg);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border-radius: 1rem;
    border: 1px solid var(--glass-border);
    overflow: hidden;
  }

  #hero-heading .rtl-text {
    font-size: clamp(1.5rem, 7vw, 2.5rem);
    line-height: 1.2;
    margin-bottom: 0.5rem;
    display: block;
    word-wrap: break-word;
  }

  #hero-heading .eyebrow-text {
    font-size: 0.85rem;
    letter-spacing: 0.5px;
    margin-bottom: 0.5rem;
    display: block;
    white-space: normal;
    word-wrap: break-word;
    opacity: 0.9;
    font-weight: 600;
    text-transform: uppercase;
  }

  .hero .tagline {
    font-size: 0.9rem;
    line-height: 1.6;
    margin-bottom: 1.5rem;
    padding: 0 0.5rem;
  }

  .hero-cta-group {
    gap: 0.75rem;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
  }

  .hero-btn {
    width: auto;
    min-width: 120px;
    padding: 0.6rem 1rem;
    font-size: 0.9rem;
    flex: 1;
    white-space: nowrap;
    justify-content: center;
  }

  .social-links-hero {
    gap: 1rem;
    margin-top: 1.25rem;
  }

  .social-links-hero a {
    width: 42px;
    height: 42px;
    font-size: 1.1rem;
  }
}

/* Small Mobile Devices */
@media (max-width: 480px) {
  .hero {
    padding-top: 4.5rem;
    padding-bottom: 2rem;
  }

  .hero-content-wrapper {
    margin: 0 0.75rem;
    padding: 1.25rem 0.5rem;
  }

  .hero .profile-image {
    width: 130px;
    height: 130px;
  }

  #hero-heading .rtl-text {
    font-size: 1.6rem;
  }

  #hero-heading .eyebrow-text {
    font-size: 0.75rem;
  }

  .hero-cta-group {
    flex-direction: column;
    align-items: stretch;
  }

  .hero-btn {
    width: 100%;
    margin-bottom: 0.25rem;
  }
}

/* ------------------------------------------------------------
  Core hero visuals
  ------------------------------------------------------------ */

.hero .profile-image {
  border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  border: 4px solid transparent;
  background:
    linear-gradient(var(--card-bg, #fff), var(--card-bg, #fff)) padding-box,
    var(--hero-text-gradient) border-box;
  box-shadow: 0 20px 50px var(--hero-shadow-color),
              inset 0 0 20px rgba(79, 70, 229, 0.1);
  transition: transform 0.55s var(--hero-ease),
              box-shadow 0.55s var(--hero-ease),
              filter 0.55s var(--hero-ease);
  animation: morph-border 8s ease-in-out infinite alternate;
  object-fit: cover;
  filter: grayscale(10%);
  will-change: transform;
}

/* Hover فقط برای دستگاه‌های hoverدار */
@media (hover: hover) {
  .hero .profile-image:hover {
    transform: scale(1.05) rotate(2deg);
    box-shadow: 0 30px 60px var(--hero-shadow-color);
    filter: grayscale(0%);
  }
}

@keyframes morph-border {
  0% { border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%; }
  50% { border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%; }
  100% { border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%; }
}

@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(calc(-1 * var(--hero-float))); }
  100% { transform: translateY(0px); }
}

.hero-image-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  animation: float 7s ease-in-out infinite;
  will-change: transform;
}

/* Decorative elements behind image */
.hero-image-wrapper::before {
  content: "";
  position: absolute;
  width: 120%;
  height: 120%;
  background: radial-gradient(circle, var(--hero-shadow-color) 0%, transparent 70%);
  opacity: 0.5;
  z-index: -1;
  animation: pulse-glow 5s infinite alternate;
}

@keyframes pulse-glow {
  0% { transform: scale(0.9); opacity: 0.4; }
  100% { transform: scale(1.1); opacity: 0.6; }
}



.hero-content-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: clamp(1.5rem, 3vw, 3.25rem) clamp(1.25rem, 2.6vw, 3.5rem);
  border-radius: var(--hero-radius);
  background: var(--glass-bg);
  backdrop-filter: blur(var(--hero-blur));
  -webkit-backdrop-filter: blur(var(--hero-blur));

  /* color-mix fallback */
  border: 1px solid rgba(255, 255, 255, 0.5);
  box-shadow:
    var(--glass-shadow),
    inset 0 0 0 1px rgba(255, 255, 255, 0.14),
    0 1px 0 rgba(255, 255, 255, 0.14);

  transition: transform 0.45s var(--hero-ease),
              box-shadow 0.45s var(--hero-ease),
              border-color 0.45s var(--hero-ease);
  margin-top: 2rem;
  max-width: min(100%, calc(var(--hero-content-max) + 14ch));
  will-change: transform;
  position: relative;
}

/* Upgrade border when color-mix is supported */
@supports (color: color-mix(in srgb, #fff, #000)) {
  .hero-content-wrapper {
    border: 1px solid color-mix(in srgb, var(--glass-border), rgba(79, 70, 229, 0.18));
  }
}

/* Hover فقط برای دستگاه‌های hoverدار */
@media (hover: hover) {
  .hero-content-wrapper:hover {
    transform: translateY(-5px) scale(1.01);
    box-shadow: var(--hero-shadow-strong);
    border-color: rgba(255, 255, 255, 0.8);
  }
}

/* Edge highlight on the glass card */
.hero-content-wrapper::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: inherit;
  padding: 1px;
  background: linear-gradient(135deg, rgba(79,70,229,0.32), rgba(6,182,212,0.18), rgba(244,63,94,0.18));
  -webkit-mask:
    linear-gradient(#000 0 0) content-box,
    linear-gradient(#000 0 0);
  mask:
    linear-gradient(#000 0 0) content-box,
    linear-gradient(#000 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  opacity: 0.65;
  pointer-events: none;
}

@media (min-width: 992px) {
  .hero-content-wrapper {
    align-items: flex-start;
    padding: 3.5rem;
    text-align: start;
  }
}

#hero-heading {
  font-weight: 900;
  line-height: 1.1;
  margin-bottom: 1.5rem;
  position: relative;
}

.eyebrow-text {
  display: block;
  font-family: "Montserrat", sans-serif;
  font-size: clamp(0.9rem, 1.1vw, 1.1rem);
  font-weight: 700;
  color: var(--primary-color);
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.16em;
  opacity: 0.9;
  background: linear-gradient(to right, var(--hero-gradient-start), var(--hero-gradient-end));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.rtl-text {
  display: block;
  font-family: "Vazirmatn", sans-serif;
  font-size: clamp(2.15rem, 6vw, 4.6rem);
  font-weight: 900;
  background: linear-gradient(135deg, var(--hero-gradient-start), var(--hero-gradient-end), var(--hero-accent));
  background-size: 200% auto;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
  text-shadow: 0 10px 30px rgba(99, 102, 241, 0.15);
  animation: gradient-text-flow 4.5s linear infinite;
  padding-top: 0.2em;
  padding-bottom: 0.1em;
  line-height: 1.3;
}

@keyframes gradient-text-flow {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

.hero .tagline {
  font-size: clamp(1rem, 1.35vw, 1.35rem);
  color: var(--text-color);
  margin-bottom: 2.5rem;
  line-height: 1.8;
  max-width: var(--hero-content-max);
  font-weight: 500;
  text-wrap: pretty;
}

.hero .tagline span {
  font-weight: 800;
  color: var(--primary-color);
  position: relative;
  display: inline-block;
}

.hero-cta-group {
  display: flex;
  flex-wrap: wrap;
  gap: 0.9rem;
  justify-content: center;
  margin-bottom: 2.5rem;
  width: 100%;
}

@media (min-width: 992px) {
  .hero-cta-group {
    justify-content: flex-start;
  }
}

.hero-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.9rem 2.25rem;
  font-size: 1.05rem;
  font-weight: 700;
  border-radius: 50px;
  text-decoration: none;
  transition: transform 0.28s var(--hero-ease),
              box-shadow 0.28s var(--hero-ease),
              background-color 0.28s var(--hero-ease),
              color 0.28s var(--hero-ease),
              border-color 0.28s var(--hero-ease),
              filter 0.28s var(--hero-ease);
  position: relative;
  overflow: hidden;
  z-index: 1;
  letter-spacing: 0.5px;
  -webkit-tap-highlight-color: transparent;
  transform: translateZ(0);
}

.hero-btn i { margin-inline-end: 0.5rem; }
[dir="rtl"] .hero-btn i {
  margin-inline-end: 0;
  margin-inline-start: 0.5rem;
}

.hero-btn.primary {
  background: var(--hero-text-gradient);
  color: white;
  box-shadow: 0 10px 25px rgba(99, 102, 241, 0.4);
  border: none;
  background-size: 140% 140%;
}

.hero-btn.secondary {
  background: rgba(255, 255, 255, 0.1);
  color: var(--text-color);
  border: 2px solid var(--primary-color);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}

/* Hover فقط روی hover-capable */
@media (hover: hover) {
  .hero-btn.primary:hover {
    filter: saturate(112%) contrast(108%);
    transform: translateY(-4px);
    box-shadow: 0 20px 40px rgba(99, 102, 241, 0.6);
  }

  .hero-btn.secondary:hover {
    background-color: var(--primary-color);
    color: white;
    transform: translateY(-4px);
    box-shadow: 0 10px 25px rgba(99, 102, 241, 0.2);
  }
}

/* Shine sweep */
.hero-btn.primary::after {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent);
  transition: 0.5s;
}

@media (hover: hover) {
  .hero-btn.primary:hover::after {
    left: 100%;
  }
}

.hero-btn:active {
  transform: translateY(-2px);
}

.hero-btn.full-width-btn {
  width: 100%;
}

.social-links-hero {
  display: flex;
  gap: 1.5rem;
  font-size: 1.5rem;
  margin-top: 0.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

@media (min-width: 992px) {
  .social-links-hero { justify-content: flex-start; }
}

.social-links-hero a {
  color: var(--text-color-light, #64748b);
  transition: transform 0.35s var(--hero-ease),
              box-shadow 0.35s var(--hero-ease),
              background-color 0.35s var(--hero-ease),
              color 0.35s var(--hero-ease),
              border-color 0.35s var(--hero-ease);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.52);
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
  box-shadow: 0 4px 10px rgba(0,0,0,0.05);
  border: 1px solid rgba(255,255,255,0.4);
  -webkit-tap-highlight-color: transparent;
}

/* Touch feedback */
.social-links-hero a:active {
  transform: translateY(-2px) scale(1.04);
}

/* Hover only on hover-capable devices */
@media (hover: hover) {
  .social-links-hero a:hover {
    transform: translateY(-5px) scale(1.1);
    color: white;
    box-shadow: 0 10px 25px rgba(0,0,0,0.15);
    border-color: transparent;
  }

  .social-links-hero a:nth-child(1):hover { background-color: #333; }
  .social-links-hero a:nth-child(2):hover { background-color: #0077b5; }
  .social-links-hero a:nth-child(3):hover { background-color: #0088cc; }
  .social-links-hero a:nth-child(4):hover {
    background: radial-gradient(circle at 30% 107%,
      #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
  }
}

/* Scroll Down Indicator */
.scroll-down-indicator {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0.8;
  transition: opacity 0.3s;
  cursor: pointer;
  z-index: 10;
}

.scroll-down-indicator a {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  gap: 0.35rem;
  padding: 0.5rem;
  border-radius: 999px;
  text-decoration: none;
  color: inherit;
}

/* hover فقط روی hover-capable + fallback برای color-mix */
@media (hover: hover) {
  .scroll-down-indicator a:hover {
    background: rgba(255, 255, 255, 0.22);
  }

  @supports (color: color-mix(in srgb, #fff, #000)) {
    .scroll-down-indicator a:hover {
      background: color-mix(in srgb, var(--glass-bg), transparent 55%);
    }
  }

  .scroll-down-indicator:hover { opacity: 1; }
}

/* Mobile: avoid overlaying CTA */
@media (max-width: 991px) {
  .scroll-down-indicator {
    position: static;
    margin-top: 1.5rem;
    transform: none;
  }
}

.mouse {
  width: 30px;
  height: 50px;
  border: 2px solid var(--text-color, #64748b);
  border-radius: 20px;
  position: relative;
  margin-bottom: 0.5rem;
}

.wheel {
  width: 4px;
  height: 8px;
  background-color: var(--hero-accent);
  border-radius: 2px;
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  animation: scroll-wheel 1.5s infinite;
}

.arrow-scroll span {
  display: block;
  width: 10px;
  height: 10px;
  border-bottom: 2px solid var(--text-color, #64748b);
  border-right: 2px solid var(--text-color, #64748b);
  transform: rotate(45deg);
  margin: -5px auto;
  animation: scroll-arrow 2s infinite;
}

.arrow-scroll span:nth-child(2) { animation-delay: -0.2s; }
.arrow-scroll span:nth-child(3) { animation-delay: -0.4s; }

@keyframes scroll-wheel {
  0% { top: 10px; opacity: 1; }
  100% { top: 30px; opacity: 0; }
}

@keyframes scroll-arrow {
  0% { opacity: 0; transform: rotate(45deg) translate(-5px, -5px); }
  50% { opacity: 1; }
  100% { opacity: 0; transform: rotate(45deg) translate(5px, 5px); }
}

/* Typing Effect */
.visually-hidden-seo {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.hero .tagline .typing-wrapper {
  display: inline !important;
  position: relative;
  direction: rtl;
  unicode-bidi: embed;
}

.hero .tagline .typing-text {
  display: inline !important;
  font-weight: 800;
  color: var(--text-color);
  background: var(--hero-text-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  -webkit-box-decoration-break: clone;
  box-decoration-break: clone;
  line-height: 1.4;
}

.hero .tagline .cursor {
  display: inline-block !important;
  width: 3px;
  height: 1em;
  background-color: var(--primary-color);
  animation: blink 1s step-end infinite;
  margin-right: 2px;
  vertical-align: text-bottom;
  position: relative;
  top: 1px;
}

@keyframes blink {
  0%, 49% { opacity: 1; }
  50%, 100% { opacity: 0; }
}

/* Dark mode adjustments */
@media (prefers-color-scheme: dark) {
  .hero {
    background-color: #0b1220;
  }

  .hero::before {
    opacity: 0.32;
  }

  .hero::after {
    opacity: calc(var(--hero-aurora-opacity) - 0.08);
    filter: blur(42px) saturate(120%);
  }

  .hero-btn.secondary {
    border-color: rgba(255,255,255,0.3);
    color: #f1f5f9;
  }

  .social-links-hero a {
    background-color: rgba(30, 41, 59, 0.72);
    color: #cbd5e1;
    border-color: rgba(255,255,255,0.1);
  }

  .mouse { border-color: #cbd5e1; }
  .wheel { background: #38bdf8; }
  .arrow-scroll span { border-color: #cbd5e1; }
}

/* RTL Specifics */
[dir="rtl"] .hero-content-wrapper { text-align: right; }
[dir="ltr"] .hero-content-wrapper { text-align: left; }

/* Status Badge */
.status-badge {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: rgba(16, 185, 129, 0.1);
  border: 1px solid rgba(16, 185, 129, 0.2);
  border-radius: 20px;
  color: #10b981;
  font-size: 0.875rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}

.status-dot {
  width: 8px;
  height: 8px;
  background-color: #10b981;
  border-radius: 50%;
  box-shadow: 0 0 0 2px rgba(16, 185, 129, 0.2);
  animation: pulse-green 2s infinite;
}

@keyframes pulse-green {
  0% { box-shadow: 0 0 0 0 rgba(16, 185, 129, 0.7); }
  70% { box-shadow: 0 0 0 6px rgba(16, 185, 129, 0); }
  100% { box-shadow: 0 0 0 0 rgba(16, 185, 129, 0); }
}

/* Small Mobile Devices */
/* (intentionally blank) */

/* ------------------------------------------------------------
  2026 Home Hero Override (Home-only)
  ------------------------------------------------------------ */
body.home-page #hero {
  position: relative;
  min-height: min(100svh, 980px);
  padding: clamp(5.5rem, 8vw, 8.5rem) clamp(1rem, 4vw, 3rem)
    clamp(3rem, 7vw, 5rem);
  overflow: hidden;
  isolation: isolate;
  background:
    radial-gradient(1200px 700px at 8% 10%, rgba(79, 70, 229, 0.2), transparent 62%),
    radial-gradient(1000px 680px at 95% 16%, rgba(6, 182, 212, 0.2), transparent 58%),
    linear-gradient(170deg, rgba(248, 250, 252, 0.95), rgba(241, 245, 249, 0.92));
}

body.home-page #hero .hero-grid {
  max-width: 1240px;
  margin: 0 auto;
  gap: clamp(1.5rem, 4vw, 4rem);
  align-items: center;
  position: relative;
  z-index: 2;
}

@media (min-width: 992px) {
  body.home-page #hero .hero-grid {
    grid-template-columns: 1.15fr 0.85fr;
  }
}

body.home-page #hero .hero-content-wrapper {
  align-items: flex-start;
  text-align: start;
  margin-top: 0;
  width: min(100%, 720px);
  padding: clamp(1.4rem, 2.8vw, 3rem);
  border-radius: clamp(1rem, 2vw, 1.6rem);
  background: rgba(255, 255, 255, 0.7);
  border: 1px solid rgba(255, 255, 255, 0.72);
  box-shadow:
    0 24px 64px rgba(15, 23, 42, 0.12),
    inset 0 0 0 1px rgba(255, 255, 255, 0.18);
}

body.home-page #hero #hero-heading {
  margin-bottom: 1.15rem;
}

body.home-page #hero #hero-heading .eyebrow-text {
  font-size: clamp(0.78rem, 1vw, 1rem);
  letter-spacing: 0.18em;
  opacity: 0.9;
}

body.home-page #hero #hero-heading .rtl-text {
  font-size: clamp(2.1rem, 6.2vw, 4.9rem);
  line-height: 1.22;
}

body.home-page #hero .tagline {
  font-size: clamp(1rem, 1.35vw, 1.3rem);
  line-height: 1.9;
  margin-bottom: clamp(1.1rem, 2vw, 2rem);
  max-width: 56ch;
}

body.home-page #hero .hero-cta-group {
  width: 100%;
  margin-bottom: 1.8rem;
  gap: 0.85rem;
}

body.home-page #hero .hero-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: 48px;
  padding: 0.84rem 1.55rem;
  border-radius: 999px;
  text-decoration: none;
  font-weight: 700;
  line-height: 1.3;
  transition:
    transform 0.28s var(--hero-ease),
    box-shadow 0.28s var(--hero-ease),
    background-color 0.28s var(--hero-ease),
    color 0.28s var(--hero-ease),
    border-color 0.28s var(--hero-ease),
    filter 0.28s var(--hero-ease);
}

body.home-page #hero .hero-btn.primary {
  animation: none;
  color: #fff;
  background: linear-gradient(
    135deg,
    color-mix(in srgb, var(--hero-gradient-start), #1f2937 10%),
    var(--hero-gradient-end)
  );
  box-shadow: 0 14px 32px rgba(79, 70, 229, 0.33);
}

body.home-page #hero .hero-btn.secondary {
  color: var(--text-color);
  background: rgba(255, 255, 255, 0.42);
  border-color: rgba(79, 70, 229, 0.35);
}

@media (hover: hover) {
  body.home-page #hero .hero-btn.primary:hover {
    transform: translateY(-4px);
    box-shadow: 0 24px 44px rgba(79, 70, 229, 0.44);
    filter: saturate(1.08) contrast(1.05);
  }

  body.home-page #hero .hero-btn.secondary:hover {
    transform: translateY(-3px);
    background: rgba(79, 70, 229, 0.15);
    color: var(--hero-gradient-start);
    border-color: rgba(79, 70, 229, 0.55);
  }
}

body.home-page #hero .social-links-hero {
  margin-top: 0.35rem;
  gap: 0.95rem;
}

body.home-page #hero .social-links-hero .hero-social-link {
  width: 50px;
  height: 50px;
  text-decoration: none;
  color: var(--text-color-light);
  border-color: rgba(255, 255, 255, 0.65);
}

body.home-page #hero .scroll-down-indicator {
  bottom: clamp(1rem, 2.5vw, 2.25rem);
  opacity: 0.75;
}

body.home-page #hero .scroll-down-indicator .scroll-indicator-link {
  text-decoration: none;
  color: inherit;
}

body.home-page #hero .profile-image {
  width: clamp(180px, 22vw, 290px);
  height: clamp(180px, 22vw, 290px);
  animation-duration: 10s;
}

@media (max-width: 991px) {
  body.home-page #hero {
    min-height: auto;
    padding-top: clamp(4.75rem, 9vw, 6.2rem);
    padding-bottom: 1.6rem;
  }

  body.home-page #hero .hero-grid {
    gap: 1.25rem;
    padding-inline: 0;
  }

  body.home-page #hero .hero-content-wrapper {
    width: min(100%, 26rem);
    margin: 0 auto;
    align-items: center;
    text-align: center;
    padding: 1.25rem 1rem;
  }

  body.home-page #hero #hero-heading .rtl-text {
    font-size: clamp(1.65rem, 7.5vw, 2.65rem);
  }

  body.home-page #hero .tagline {
    font-size: 0.98rem;
    line-height: 1.75;
    margin-bottom: 1.25rem;
  }

  body.home-page #hero .hero-cta-group {
    flex-direction: column;
    width: min(100%, 280px);
    margin-inline: auto;
  }

  body.home-page #hero .hero-btn {
    width: 100%;
  }

  body.home-page #hero .social-links-hero {
    justify-content: center;
  }

  body.home-page #hero .scroll-down-indicator {
    position: static;
    margin-top: 1rem;
